


































import Vue from 'vue'
import store from '@/utils/store'
export default Vue.extend({
  props: {
    value: String
  },

  data() {
    return {
      modal: false
    }
  },

  computed: {
    date: {
      get(): string {
        return this.value
      },
      set(value: string) {
        this.$emit('input', value)
      }
    }
  }
})
