
















import Vue from 'vue'
import { VMoney } from 'v-money'

export default Vue.extend({
  name: 'Cost',

  directives: {
    money : VMoney 
  },

  props: {
    value: String
  },

  data() {
    return {
      refresh: false,
      money: {
        thousands: ',',
        precision: 0,
        masked: false
      }
    }
  },

  computed: {
    cost: {
      get(): string {
        return this.value
      },
      set(value: string) {
        this.$emit('input', value ? value : '0')
      }
    }
  },

  watch: {
    cost(value: string) {
      if (value === '') {
        this.refresh = true
        setTimeout(() => this.refresh = false, 0)
      }
    }
  }
})
